import { ContactProvider } from './../../providers/contact/contact';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

/**
 * Generated class for the GatinhosEditPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-gatinhos-edit',
  templateUrl: 'gatinhos-edit.html',
})
export class GatinhosEditPage {
  form: FormGroup;
  cat: any;
  constructor(public navCtrl: NavController, public navParams: NavParams,
  private formBuilder: FormBuilder, private provider: ContactProvider,
  private toast: ToastController) {
    this.cat = this.navParams.data.drug || { };
    this.createFormCat();
  }

  createFormCat() {
    this.form = this.formBuilder.group({
      key: [this.cat.key],
      nome: [this.cat.nome, Validators.required],
      idade: [this.cat.idade],
      dono: [this.cat.dono, Validators.required],
      doenca: [this.cat.doenca, Validators.required],
    });
  }

  onSubmitCat() {
    if (this.form.valid) {
      this.provider.saveG(this.form.value)
        .then(() => {
          this.toast.create({ message: 'Contato salvo com sucesso.', duration: 3000 }).present();
          this.navCtrl.pop();
        })
        .catch((e) => {
          this.toast.create({ message: 'Erro ao salvar o contato.', duration: 3000 }).present();
          console.error(e);
        })
    }
  }

}



